<template>
<div :style='{"border":"2px solid #a4d9b7","padding":"20px","margin":"20px auto 0","borderRadius":"8px","flexWrap":"wrap","background":"#fff","width":"1200px","position":"relative"}'>
    <el-form
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="200px"
    >
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="图书名称" prop="tushumingcheng">
            <el-input v-model="ruleForm.tushumingcheng" 
                placeholder="图书名称" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="图书类型" prop="tushuleixing">
            <el-input v-model="ruleForm.tushuleixing" 
                placeholder="图书类型" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="作者" prop="zuozhe">
            <el-input v-model="ruleForm.zuozhe" 
                placeholder="作者" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="出版社" prop="chubanshe">
            <el-input v-model="ruleForm.chubanshe" 
                placeholder="出版社" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="归还数量" prop="kucun">
            <el-input v-model="ruleForm.kucun" 
                placeholder="归还数量" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="归还时间" prop="guihaishijian" disabled>
              <el-date-picker
                  value-format="yyyy-MM-dd HH:mm:ss"
                  v-model="ruleForm.guihaishijian" 
                  type="datetime"
                  placeholder="归还时间">
              </el-date-picker>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}'  label="归还类型" prop="guihaileixing">
            <el-select v-model="ruleForm.guihaileixing" placeholder="请选择归还类型"  >
              <el-option
                  v-for="(item,index) in guihaileixingOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="归还备注" prop="guihaibeizhu">
            <el-input v-model="ruleForm.guihaibeizhu" 
                placeholder="归还备注" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="读者账号" prop="duzhezhanghao">
            <el-input v-model="ruleForm.duzhezhanghao" 
                placeholder="读者账号" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 0px","display":"inline-block"}' label="读者姓名" prop="duzhexingming">
            <el-input v-model="ruleForm.duzhexingming" 
                placeholder="读者姓名" clearable ></el-input>
          </el-form-item>

      <el-form-item :style='{"padding":"0","margin":"8px 0 0 0"}'>
        <el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"20px 40px 20px 260px","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"20px","background":"#47b144","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit">提交</el-button>
        <el-button :style='{"border":"2px solid #3db769","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#3db769","borderRadius":"20px","background":"rgba(255, 255, 255, 1)","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
      return {
        id: '',
        baseUrl: '',
        ro:{
            tushumingcheng : false,
            tushuleixing : false,
            zuozhe : false,
            chubanshe : false,
            kucun : false,
            guihaishijian : false,
            guihaileixing : false,
            guihaibeizhu : false,
            duzhezhanghao : false,
            duzhexingming : false,
            sfsh : false,
            shhf : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          tushumingcheng: '',
          tushuleixing: '',
          zuozhe: '',
          chubanshe: '',
          kucun: '',
          guihaishijian: '',
          guihaileixing: '',
          guihaibeizhu: '',
          duzhezhanghao: '',
          duzhexingming: '',
        },
        guihaileixingOptions: [],
        rules: {
          tushumingcheng: [
          ],
          tushuleixing: [
          ],
          zuozhe: [
          ],
          chubanshe: [
          ],
          kucun: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          guihaishijian: [
          ],
          guihaileixing: [
          ],
          guihaibeizhu: [
          ],
          duzhezhanghao: [
          ],
          duzhexingming: [
          ],
          sfsh: [
          ],
          shhf: [
          ],
        },
      };
    },
    computed: {



    },
    created() {
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
      this.ruleForm.guihaishijian = this.getCurDateTime()
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 下载
      download(file){
        window.open(`${file}`)
      },
      // 初始化
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='tushumingcheng'){
              this.ruleForm.tushumingcheng = obj[o];
              this.ro.tushumingcheng = true;
              continue;
            }
            if(o=='tushuleixing'){
              this.ruleForm.tushuleixing = obj[o];
              this.ro.tushuleixing = true;
              continue;
            }
            if(o=='zuozhe'){
              this.ruleForm.zuozhe = obj[o];
              this.ro.zuozhe = true;
              continue;
            }
            if(o=='chubanshe'){
              this.ruleForm.chubanshe = obj[o];
              this.ro.chubanshe = true;
              continue;
            }
            if(o=='kucun'){
              this.ruleForm.kucun = obj[o];
              this.ro.kucun = true;
              continue;
            }
            if(o=='guihaishijian'){
              this.ruleForm.guihaishijian = obj[o];
              this.ro.guihaishijian = true;
              continue;
            }
            if(o=='guihaileixing'){
              this.ruleForm.guihaileixing = obj[o];
              this.ro.guihaileixing = true;
              continue;
            }
            if(o=='guihaibeizhu'){
              this.ruleForm.guihaibeizhu = obj[o];
              this.ro.guihaibeizhu = true;
              continue;
            }
            if(o=='duzhezhanghao'){
              this.ruleForm.duzhezhanghao = obj[o];
              this.ro.duzhezhanghao = true;
              continue;
            }
            if(o=='duzhexingming'){
              this.ruleForm.duzhexingming = obj[o];
              this.ro.duzhexingming = true;
              continue;
            }
          }
        }
        // 获取用户信息
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
            if((json.duzhezhanghao!=''&&json.duzhezhanghao) || json.duzhezhanghao==0){
                this.ruleForm.duzhezhanghao = json.duzhezhanghao
            }
            if((json.duzhexingming!=''&&json.duzhexingming) || json.duzhexingming==0){
                this.ruleForm.duzhexingming = json.duzhexingming
            }
          }
        });
        this.guihaileixingOptions = "线上归还,线下归还".split(',')
      },

    // 多级联动参数
      // 多级联动参数
      info(id) {
        this.$http.get('tushuguihai/detail/${id}', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 提交
      onSubmit() {

        //更新跨表属性
        var crossuserid;
        var crossrefid;
        var crossoptnum;
        this.$refs["ruleForm"].validate(valid => {
          if(valid) {
            if(this.type=='cross'){
                 var statusColumnName = localStorage.getItem('statusColumnName');
                 var statusColumnValue = localStorage.getItem('statusColumnValue');
                 if(statusColumnName && statusColumnName!='') {
                     var obj = JSON.parse(localStorage.getItem('crossObj'));
                     if(!statusColumnName.startsWith("[")) {
                         for (var o in obj){
                             if(o==statusColumnName){
                                 obj[o] = statusColumnValue;
                             }
                         }
                         var table = localStorage.getItem('crossTable');
                         this.$http.post(table+'/update', obj).then(res => {});
                     } else {
                            crossuserid=Number(localStorage.getItem('userid'));
                            crossrefid=obj['id'];
                            crossoptnum=localStorage.getItem('statusColumnName');
                            crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                     }
                 }
            }
            if(crossrefid && crossuserid) {
                 this.ruleForm.crossuserid=crossuserid;
                 this.ruleForm.crossrefid=crossrefid;
                 var params = {
                     page: 1,
                     limit: 10,
                     crossuserid:crossuserid,
                     crossrefid:crossrefid,
                 }
                 this.$http.get('tushuguihai/list', {
                  params: params
                 }).then(res => {
                     if(res.data.data.total>=crossoptnum) {
                         this.$message({
                          message: localStorage.getItem('tips'),
                          type: 'success',
                          duration: 1500,
                         });
                          return false;
                     } else {
                         // 跨表计算


                          this.$http.post('tushuguihai/add', this.ruleForm).then(res => {
                              if (res.data.code == 0) {
                                  this.$message({
                                      message: '操作成功',
                                      type: 'success',
                                      duration: 1500,
                                      onClose: () => {
                                          this.$router.go(-1);
                                      }
                                  });
                              } else {
                                  this.$message({
                                      message: res.data.msg,
                                      type: 'error',
                                      duration: 1500
                                  });
                              }
                          });
                     }
                 });
             } else {


                  this.$http.post('tushuguihai/add', this.ruleForm).then(res => {
                     if (res.data.code == 0) {
                          this.$message({
                              message: '操作成功',
                              type: 'success',
                              duration: 1500,
                              onClose: () => {
                                  this.$router.go(-1);
                              }
                          });
                      } else {
                          this.$message({
                              message: res.data.msg,
                              type: 'error',
                              duration: 1500
                          });
                      }
                  });
             }
          }
        });
      },
      // 获取uuid
      getUUID () {
        return new Date().getTime();
      },
      // 返回
      back() {
        this.$router.go(-1);
      },
    }
  };
</script>

